import React from 'react';
import logo from './logo.svg';
import 'materialize-css';
import { Row, Col, Navbar, Modal, Button, Error } from 'react-materialize';
import './App.css';
import Home from './Home';
import About from './About';
import Shop from "./Shop";
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';

class Call_modal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showWarning: true };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }

  handleToggleClick() {
    this.setState(state => ({
      showWarning: !state.showWarning
    }));
  }
  render() {
    return (
      <a onClick={this.handleToggleClick}>
        {this.state.showWarning ?
          <div className="fixed-action-btn">
            <a className="btn-floating btn-large red">
              <i className="large material-icons">local_phone</i>
            </a>
          </div >
          : <Modal
            name='action'
            className='center-align'
            actions={[
              <div className="fixed-action-btn">
                <Button flat modal="close" node="button" waves="green">Close</Button>
              </div>
            ]}
            bottomSheet={false}
            fixedFooter={false}
            header="CONTACT"
            id="modal1"
            open={true}
            options={{
              dismissible: true,
              endingTop: '10%',
              inDuration: 250,
              onCloseEnd: null,
              onCloseStart: null,
              onOpenEnd: null,
              onOpenStart: null,
              opacity: 0.5,
              outDuration: 250,
              preventScrolling: true,
              startingTop: '4%'
            }} >
            <div class="divider" /><br />
            <Row>
              <Col s={12}>
                <a href="tel://+4804950707" class="btn btn-block waves-effect waves-light" >
                  <i class="material-icons">local_phone</i> (480) 495-0707

                  </a>
              </Col>
            </Row>
            <Row>
              <Col s={12}>
                <a href='mailTo://larry.kohler@guardian1.life' class="btn btn-block waves-effect waves-light"  >
                  <i class="material-icons ">mail</i> larry.kohler@guardian1.life
                  </a>
              </Col>
            </Row>
          </Modal>
        }
      </a>

    );
  }
}

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Navbar

          brand={
            <Link to="/">
              <img
                className='App-logo valign-wrapper'
                src={logo}
                alt="logo" />
            </Link>
          }
          centerLogo
          className=' grey darken-4'>
          <Link to="/">Home </Link>
          <Link to="/about">About Us </Link>
          <Link to="/shop">Shop Now </Link>
        </Navbar>

        <Call_modal />
        <header className="App-header">
          <Switch>
            <Route path="/" component={Home} exact />
            <Route path="/about" component={About} />
            <Route path="/shop" component={Shop} />
            <Route component={Error} />
          </Switch>
        </header>
      </BrowserRouter>
    </div>
  );
}

export default App;